'use client';

import { createContext, useContext, useState, useEffect, ReactNode } from 'react';

interface ZoomContextType {
  zoomLevel: number;
  setZoomLevel: (level: number) => void;
  increaseZoom: () => void;
  decreaseZoom: () => void;
  resetZoom: () => void;
  zoomLevels: number[];
}

const ZoomContext = createContext<ZoomContextType | undefined>(undefined);

const ZOOM_LEVELS = [100, 125, 150, 175, 200, 250, 300];

export function ZoomProvider({ children }: { children: ReactNode }) {
  const [zoomLevel, setZoomLevelState] = useState(200); // Default to 200% for better visibility

  const setZoomLevel = (level: number) => {
    if (ZOOM_LEVELS.includes(level)) {
      setZoomLevelState(level);
      // Apply zoom to CSS custom property
      document.documentElement.style.setProperty('--zoom-scale', (level / 100).toString());
      document.documentElement.className = `zoom-${level}`;
    }
  };

  const increaseZoom = () => {
    const currentIndex = ZOOM_LEVELS.indexOf(zoomLevel);
    if (currentIndex < ZOOM_LEVELS.length - 1) {
      setZoomLevel(ZOOM_LEVELS[currentIndex + 1]);
    }
  };

  const decreaseZoom = () => {
    const currentIndex = ZOOM_LEVELS.indexOf(zoomLevel);
    if (currentIndex > 0) {
      setZoomLevel(ZOOM_LEVELS[currentIndex - 1]);
    }
  };

  const resetZoom = () => {
    setZoomLevel(100);
  };

  // Apply initial zoom on mount
  useEffect(() => {
    setZoomLevel(zoomLevel);

    // Listen for zoom keyboard events
    const handleZoomIn = () => increaseZoom();
    const handleZoomOut = () => decreaseZoom();
    const handleZoomReset = () => resetZoom();

    window.addEventListener('zoomIn', handleZoomIn);
    window.addEventListener('zoomOut', handleZoomOut);
    window.addEventListener('zoomReset', handleZoomReset);

    // Global keyboard shortcuts
    const handleKeyDown = (e: KeyboardEvent) => {
      if (e.ctrlKey || e.metaKey) {
        if (e.key === '=' || e.key === '+') {
          e.preventDefault();
          increaseZoom();
        } else if (e.key === '-') {
          e.preventDefault();
          decreaseZoom();
        } else if (e.key === '0') {
          e.preventDefault();
          resetZoom();
        }
      }
    };

    document.addEventListener('keydown', handleKeyDown);

    return () => {
      window.removeEventListener('zoomIn', handleZoomIn);
      window.removeEventListener('zoomOut', handleZoomOut);
      window.removeEventListener('zoomReset', handleZoomReset);
      document.removeEventListener('keydown', handleKeyDown);
    };
  }, [zoomLevel]);

  return (
    <ZoomContext.Provider value={{
      zoomLevel,
      setZoomLevel,
      increaseZoom,
      decreaseZoom,
      resetZoom,
      zoomLevels: ZOOM_LEVELS
    }}>
      {children}
    </ZoomContext.Provider>
  );
}

export function useZoom() {
  const context = useContext(ZoomContext);
  if (context === undefined) {
    throw new Error('useZoom must be used within a ZoomProvider');
  }
  return context;
}
