'use client';

import { useZoom } from '@/contexts/ZoomContext';
import { useState } from 'react';

export default function ZoomControls() {
  const { zoomLevel, increaseZoom, decreaseZoom, resetZoom, setZoomLevel, zoomLevels } = useZoom();
  const [showControls, setShowControls] = useState(false);

  return (
    <div className="relative">
      {/* Zoom Toggle Button */}
      <button
        onClick={() => setShowControls(!showControls)}
        className="flex items-center space-x-2 px-3 py-2 bg-orange-600 hover:bg-orange-700 text-white rounded-md font-medium transition-all duration-200"
        title={`Current zoom: ${zoomLevel}%`}
      >
        <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M10 8v6m3-3H7" />
        </svg>
        <span className="text-sm">{zoomLevel}%</span>
      </button>

      {/* Zoom Controls Dropdown */}
      {showControls && (
        <div className="absolute top-full right-0 mt-2 bg-white border border-gray-200 rounded-lg shadow-lg p-4 z-50 min-w-[200px]">
          <div className="space-y-3">
            {/* Zoom Level Display */}
            <div className="text-center">
              <span className="text-lg font-semibold text-gray-800">{zoomLevel}%</span>
            </div>

            {/* Zoom Buttons */}
            <div className="flex space-x-2">
              <button
                onClick={decreaseZoom}
                disabled={zoomLevel === Math.min(...zoomLevels)}
                className="flex-1 px-3 py-2 bg-gray-600 hover:bg-gray-700 disabled:bg-gray-300 text-white rounded text-sm font-medium transition-colors"
              >
                ‚àí
              </button>
              <button
                onClick={resetZoom}
                className="flex-1 px-3 py-2 bg-blue-600 hover:bg-blue-700 text-white rounded text-sm font-medium transition-colors"
              >
                100%
              </button>
              <button
                onClick={increaseZoom}
                disabled={zoomLevel === Math.max(...zoomLevels)}
                className="flex-1 px-3 py-2 bg-gray-600 hover:bg-gray-700 disabled:bg-gray-300 text-white rounded text-sm font-medium transition-colors"
              >
                +
              </button>
            </div>

            {/* Zoom Presets */}
            <div className="space-y-1">
              <div className="text-xs text-gray-600 font-medium">Quick Zoom:</div>
              <div className="grid grid-cols-3 gap-1">
                {[125, 150, 200, 250, 300].map((level) => (
                  <button
                    key={level}
                    onClick={() => setZoomLevel(level)}
                    className={`px-2 py-1 text-xs rounded transition-colors ${
                      zoomLevel === level
                        ? 'bg-orange-600 text-white'
                        : 'bg-gray-100 hover:bg-gray-200 text-gray-700'
                    }`}
                  >
                    {level}%
                  </button>
                ))}
              </div>
            </div>

            {/* Visibility Note */}
            <div className="text-xs text-gray-500 text-center pt-2 border-t">
              üîç Enhanced visibility for better reading
            </div>
          </div>
        </div>
      )}

      {/* Click outside to close */}
      {showControls && (
        <div
          className="fixed inset-0 z-40"
          onClick={() => setShowControls(false)}
        />
      )}
    </div>
  );
}
