'use client';
import Link from 'next/link'
import { useState, useEffect } from 'react'
import { useRouter } from 'next/navigation'
import Logo from './Logo'
import SearchBar from './SearchBar'
import ZoomControls from './ZoomControls'
import { useSearch } from '@/contexts/SearchContext'
import { scrollToProducts } from '@/utils/scroll'

export default function Navbar() {
  const router = useRouter();
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false)
  const [isScrolled, setIsScrolled] = useState(false)
  const [windowWidth, setWindowWidth] = useState(0)
  const { searchQuery, setSearchQuery } = useSearch();

  // Amazon-like scroll and resize detection
  useEffect(() => {
    const handleScroll = () => {
      setIsScrolled(window.scrollY > 10);
    };

    const handleResize = () => {
      setWindowWidth(window.innerWidth);
    };

    // Set initial values
    if (typeof window !== 'undefined') {
      handleScroll();
      handleResize();
    }

    // Add event listeners
    window.addEventListener('scroll', handleScroll);
    window.addEventListener('resize', handleResize);

    // Cleanup
    return () => {
      window.removeEventListener('scroll', handleScroll);
      window.removeEventListener('resize', handleResize);
    };
  }, []);

  const handleSearch = (value: string) => {
    setSearchQuery(value);
  }

  const handleSearchSubmit = () => {
    // Only proceed if there's a search query
    if (searchQuery.trim()) {
      // Check current page location
      const currentPath = window.location.pathname;
      
      if (currentPath === '/') {
        // If on home page, just scroll to products section to show search results
        scrollToProducts();
      } else {
        // If on another page, navigate to home page first
        router.push('/');
        // Small delay to ensure navigation completes before scrolling
        setTimeout(() => {
          scrollToProducts();
        }, 300);
      }
    }
  }

  const scrollToSection = (sectionId: string) => (e: React.MouseEvent) => {
    e.preventDefault();
    
    // Update URL hash without reload
    history.pushState(null, '', `/#${sectionId}`);
    
    const section = document.getElementById(sectionId);
    const navbar = document.querySelector('nav');
    
    if (section && navbar) {
      const navbarHeight = navbar.getBoundingClientRect().height;
      const elementPosition = section.getBoundingClientRect().top + window.pageYOffset;
      // Account for navbar height and add small padding
      const offsetPosition = elementPosition - navbarHeight - 16;
      
      // Smooth scroll to section
      window.scrollTo({
        top: offsetPosition,
        behavior: 'smooth'
      });
      
      // Close mobile menu if open
      setIsMobileMenuOpen(false);
    }
  }

  return (
    <nav className={`bg-white fixed w-full z-50 transition-all duration-300 ${
      isScrolled ? 'shadow-lg' : 'shadow-md'
    }`}>
      {/* Amazon-style main navigation */}
      <div className="max-w-screen-2xl mx-auto">
        <div className={`flex items-center justify-between transition-all duration-300 ${
          windowWidth < 480 ? 'h-12 px-2' :
          windowWidth < 768 ? 'h-14 px-3' :
          windowWidth < 1024 ? 'h-16 px-4' :
          windowWidth < 1440 ? 'h-18 px-6' : 'h-20 px-8'
        }`}>
          
          {/* Logo with Amazon-style scaling */}
          <div className={`flex-shrink-0 ${
            windowWidth < 480 ? 'mr-2' : 
            windowWidth < 768 ? 'mr-3' : 'mr-4'
          }`}>
            <Link 
              href="/" 
              className={`flex items-center transition-all duration-200 hover:scale-105 ${
                windowWidth < 480 ? 'scale-90' : 'scale-100'
              }`}
              onClick={(e) => {
                e.preventDefault();
                
                // Clear search, category filters, and URL hash
                setSearchQuery('');
                window.dispatchEvent(new CustomEvent('clearCategoryAndShowAll'));
                history.replaceState(null, '', '/');
                
                // Reset scroll position to top
                window.scrollTo({
                  top: 0,
                  behavior: 'smooth'
                });
                
                // Close mobile menu
                setIsMobileMenuOpen(false);
                
                // Force router refresh to ensure clean slate
                router.refresh();
              }}
            >
              <Logo />
            </Link>
          </div>

          {/* Amazon-style Search Bar - Responsive */}
          {windowWidth >= 768 && (
            <div className={`flex-1 transition-all duration-300 ${
              windowWidth < 1024 ? 'max-w-md mx-4' :
              windowWidth < 1440 ? 'max-w-lg mx-6' : 'max-w-2xl mx-8'
            }`}>
              <SearchBar 
                value={searchQuery}
                onChange={handleSearch}
                onEnter={handleSearchSubmit}
                placeholder="Search for products..."
              />
            </div>
          )}

          {/* Mobile Search Button (Amazon-style) */}
          {windowWidth < 768 && !isMobileMenuOpen && (
            <div className="flex-1 mx-2">
              <button
                onClick={() => setIsMobileMenuOpen(true)}
                className="w-full h-8 bg-gray-100 rounded-md flex items-center px-3 text-gray-500 text-sm hover:bg-gray-200 transition-colors"
              >
                <svg className="w-4 h-4 mr-2" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
                </svg>
                Search...
              </button>
            </div>
          )}

          {/* Desktop Navigation with Amazon-style responsive spacing */}
          <div className={`hidden lg:flex items-center transition-all duration-300 ${
            windowWidth < 1440 ? 'space-x-4' : 'space-x-6'
          }`}>
            {/* Zoom Controls */}
            <ZoomControls />
            
            <Link 
              href="/"
              className={`text-gray-700 hover:text-orange-600 transition-all duration-200 rounded-md font-medium border-b-2 border-transparent hover:border-orange-600 ${
                windowWidth < 1440 ? 'px-2 py-2 text-sm' : 'px-3 py-2 text-sm'
              }`}
              onClick={(e) => {
                e.preventDefault();
                
                // Check current page location
                const currentPath = window.location.pathname;
                
                if (currentPath === '/') {
                  // If already on home page, just scroll to top
                  setSearchQuery('');
                  window.dispatchEvent(new CustomEvent('clearCategoryAndShowAll'));
                  
                  window.scrollTo({
                    top: 0,
                    behavior: 'smooth'
                  });
                } else {
                  // If on another page, navigate to home page
                  setSearchQuery('');
                  window.dispatchEvent(new CustomEvent('clearCategoryAndShowAll'));
                  router.push('/');
                  
                  // Small delay to ensure navigation completes before scrolling
                  setTimeout(() => {
                    window.scrollTo({
                      top: 0,
                      behavior: 'smooth'
                    });
                  }, 300);
                }

                // Reset mobile menu state
                setIsMobileMenuOpen(false);
              }}
            >
              Home
            </Link>
            <Link 
              href="#all-products"
              className={`text-gray-700 hover:text-orange-600 transition-all duration-200 rounded-md font-medium border-b-2 border-transparent hover:border-orange-600 ${
                windowWidth < 1440 ? 'px-2 py-2 text-sm' : 'px-3 py-2 text-sm'
              }`}
              onClick={(e) => {
                e.preventDefault();
                setSearchQuery(''); // Clear any search
                
                // Check current page location
                const currentPath = window.location.pathname;
                
                if (currentPath === '/') {
                  // If on home page, just scroll to products section
                  // Dispatch event to clear category and show all products
                  window.dispatchEvent(new CustomEvent('clearCategoryAndShowAll'));
                  
                  // Get navbar height for offset
                  const navbar = document.querySelector('nav');
                  const productsSection = document.getElementById('all-products');
                  
                  if (productsSection && navbar) {
                    const navbarHeight = navbar.getBoundingClientRect().height;
                    const elementPosition = productsSection.getBoundingClientRect().top;
                    const offsetPosition = elementPosition + window.scrollY - navbarHeight - 16;
                    
                    window.scrollTo({
                      top: offsetPosition,
                      behavior: 'smooth'
                    });
                  }
                } else {
                  // If on another page, navigate to home page first
                  router.push('/');
                  // Small delay to ensure navigation completes before scrolling
                  setTimeout(() => {
                    window.dispatchEvent(new CustomEvent('clearCategoryAndShowAll'));
                    scrollToProducts();
                  }, 300);
                }
                
                setIsMobileMenuOpen(false); // Close mobile menu if open
              }}
            >
              Products
            </Link>
            <Link 
              href="#about" 
              className={`text-gray-700 hover:text-orange-600 transition-all duration-200 rounded-md font-medium border-b-2 border-transparent hover:border-orange-600 ${
                windowWidth < 1440 ? 'px-2 py-2 text-sm' : 'px-3 py-2 text-sm'
              }`}
              onClick={(e) => {
                e.preventDefault();
                
                // Check current page location
                const currentPath = window.location.pathname;
                
                if (currentPath === '/') {
                  // If on home page, just scroll to about section
                  scrollToSection('about')(e);
                } else {
                  // If on another page, navigate to home page first
                  router.push('/');
                  // Small delay to ensure navigation completes before scrolling
                  setTimeout(() => {
                    const section = document.getElementById('about');
                    const navbar = document.querySelector('nav');
                    
                    if (section && navbar) {
                      const navbarHeight = navbar.getBoundingClientRect().height;
                      const elementPosition = section.getBoundingClientRect().top + window.pageYOffset;
                      const offsetPosition = elementPosition - navbarHeight - 16;
                      
                      window.scrollTo({
                        top: offsetPosition,
                        behavior: 'smooth'
                      });
                    }
                  }, 300);
                }
                
                setIsMobileMenuOpen(false); // Close mobile menu if open
              }}
            >
              About
            </Link>
            <Link 
              href="#contact" 
              className={`text-gray-700 hover:text-orange-600 transition-all duration-200 rounded-md font-medium border-b-2 border-transparent hover:border-orange-600 ${
                windowWidth < 1440 ? 'px-2 py-2 text-sm' : 'px-3 py-2 text-sm'
              }`}
              onClick={(e) => {
                e.preventDefault();
                
                // Check current page location
                const currentPath = window.location.pathname;
                
                if (currentPath === '/') {
                  // If on home page, just scroll to contact section
                  scrollToSection('contact')(e);
                } else {
                  // If on another page, navigate to home page first
                  router.push('/');
                  // Small delay to ensure navigation completes before scrolling
                  setTimeout(() => {
                    const section = document.getElementById('contact');
                    const navbar = document.querySelector('nav');
                    
                    if (section && navbar) {
                      const navbarHeight = navbar.getBoundingClientRect().height;
                      const elementPosition = section.getBoundingClientRect().top + window.pageYOffset;
                      const offsetPosition = elementPosition - navbarHeight - 16;
                      
                      window.scrollTo({
                        top: offsetPosition,
                        behavior: 'smooth'
                      });
                    }
                  }, 300);
                }
                
                setIsMobileMenuOpen(false); // Close mobile menu if open
              }}
            >
              Contact
            </Link>
          </div>

          {/* Mobile menu button with Amazon-style responsive sizing */}
          <div className="lg:hidden flex items-center">
            <button
              onClick={() => setIsMobileMenuOpen(!isMobileMenuOpen)}
              className={`inline-flex items-center justify-center rounded-md text-gray-700 hover:text-orange-600 focus:outline-none transition-all duration-200 ${
                windowWidth < 480 ? 'p-1' : 'p-2'
              }`}
            >
              <svg
                className={`transition-all duration-200 ${
                  windowWidth < 480 ? 'h-5 w-5' : 'h-6 w-6'
                }`}
                fill="none"
                viewBox="0 0 24 24"
                stroke="currentColor"
              >
                {isMobileMenuOpen ? (
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                ) : (
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 6h16M4 12h16M4 18h16" />
                )}
              </svg>
            </button>
          </div>
        </div>
      </div>

      {/* Amazon-style Mobile Navigation */}
      {isMobileMenuOpen && (
        <div className="lg:hidden border-t border-gray-200">
          <div className={`bg-white shadow-xl transition-all duration-300 ${
            windowWidth < 480 ? 'px-2 py-3' : 'px-4 py-4'
          }`}>
            
            {/* Mobile Search Bar */}
            <div className={`${windowWidth < 480 ? 'mb-3' : 'mb-4'}`}>
              <SearchBar 
                value={searchQuery}
                onChange={handleSearch}
                onEnter={handleSearchSubmit}
                placeholder="Search for products..."
              />
            </div>

            {/* Mobile Navigation Links with Amazon-style design */}
            <div className={`space-y-1 ${windowWidth < 480 ? 'text-sm' : 'text-base'}`}>
              {/* Mobile Zoom Controls */}
              <div className={`border-b border-gray-200 pb-3 mb-3`}>
                <ZoomControls />
              </div>
              
              <Link 
                href="#"
                className={`text-gray-800 hover:text-orange-600 hover:bg-orange-50 block rounded-lg font-medium transition-all duration-200 border-l-4 border-transparent hover:border-orange-600 ${
                  windowWidth < 480 ? 'px-3 py-2' : 'px-4 py-3'
                }`}
                onClick={(e) => {
                  e.preventDefault();
                  setIsMobileMenuOpen(false);
                  
                  // Check current page location
                  const currentPath = window.location.pathname;
                  
                  if (currentPath === '/') {
                    // If already on home page, just scroll to top
                    setSearchQuery('');
                    window.dispatchEvent(new CustomEvent('clearCategoryAndShowAll'));
                    
                    window.scrollTo({
                      top: 0,
                      behavior: 'smooth'
                    });
                  } else {
                    // If on another page, navigate to home page
                    setSearchQuery('');
                    window.dispatchEvent(new CustomEvent('clearCategoryAndShowAll'));
                    router.push('/');
                    
                    // Small delay to ensure navigation completes before scrolling
                    setTimeout(() => {
                      window.scrollTo({
                        top: 0,
                        behavior: 'smooth'
                      });
                    }, 300);
                  }
                }}
              >
                üè† Home
              </Link>
              <Link 
                href="#"
                className={`text-gray-800 hover:text-orange-600 hover:bg-orange-50 block rounded-lg font-medium transition-all duration-200 border-l-4 border-transparent hover:border-orange-600 ${
                  windowWidth < 480 ? 'px-3 py-2' : 'px-4 py-3'
                }`}
                onClick={(e) => {
                  e.preventDefault();
                  setIsMobileMenuOpen(false);
                  setSearchQuery(''); // Clear any search
                  
                  // Check current page location
                  const currentPath = window.location.pathname;
                  
                  if (currentPath === '/') {
                    // If on home page, just scroll to products section
                    scrollToSection('all-products')(e);
                  } else {
                    // If on another page, navigate to home page first
                    router.push('/');
                    // Small delay to ensure navigation completes before scrolling
                    setTimeout(() => {
                      window.dispatchEvent(new CustomEvent('clearCategoryAndShowAll'));
                      scrollToProducts();
                    }, 300);
                  }
                }}
              >
                üõí Products
              </Link>
              <Link 
                href="#about" 
                className={`text-gray-800 hover:text-orange-600 hover:bg-orange-50 block rounded-lg font-medium transition-all duration-200 border-l-4 border-transparent hover:border-orange-600 ${
                  windowWidth < 480 ? 'px-3 py-2' : 'px-4 py-3'
                }`}
                onClick={(e) => {
                  e.preventDefault();
                  setIsMobileMenuOpen(false);
                  
                  // Check current page location
                  const currentPath = window.location.pathname;
                  
                  if (currentPath === '/') {
                    // If on home page, just scroll to about section
                    scrollToSection('about')(e);
                  } else {
                    // If on another page, navigate to home page first
                    router.push('/');
                    // Small delay to ensure navigation completes before scrolling
                    setTimeout(() => {
                      const section = document.getElementById('about');
                      const navbar = document.querySelector('nav');
                      
                      if (section && navbar) {
                        const navbarHeight = navbar.getBoundingClientRect().height;
                        const elementPosition = section.getBoundingClientRect().top + window.pageYOffset;
                        const offsetPosition = elementPosition - navbarHeight - 16;
                        
                        window.scrollTo({
                          top: offsetPosition,
                          behavior: 'smooth'
                        });
                      }
                    }, 300);
                  }
                }}
              >
                ‚ÑπÔ∏è About
              </Link>
              <Link 
                href="#contact" 
                className={`text-gray-800 hover:text-orange-600 hover:bg-orange-50 block rounded-lg font-medium transition-all duration-200 border-l-4 border-transparent hover:border-orange-600 ${
                  windowWidth < 480 ? 'px-3 py-2' : 'px-4 py-3'
                }`}
                onClick={(e) => {
                  e.preventDefault();
                  setIsMobileMenuOpen(false);
                  
                  // Check current page location
                  const currentPath = window.location.pathname;
                  
                  if (currentPath === '/') {
                    // If on home page, just scroll to contact section
                    scrollToSection('contact')(e);
                  } else {
                    // If on another page, navigate to home page first
                    router.push('/');
                    // Small delay to ensure navigation completes before scrolling
                    setTimeout(() => {
                      const section = document.getElementById('contact');
                      const navbar = document.querySelector('nav');
                      
                      if (section && navbar) {
                        const navbarHeight = navbar.getBoundingClientRect().height;
                        const elementPosition = section.getBoundingClientRect().top + window.pageYOffset;
                        const offsetPosition = elementPosition - navbarHeight - 16;
                        
                        window.scrollTo({
                          top: offsetPosition,
                          behavior: 'smooth'
                        });
                      }
                    }, 300);
                  }
                }}
              >
                Contact
              </Link>
            </div>
          </div>
        </div>
      )}
    </nav>
  )
}
