export const scrollToProducts = () => {
  const productsSection = document.getElementById('all-products');
  if (productsSection) {
    // Get any fixed header height
    const header = document.querySelector('header');
    const headerHeight = header ? header.getBoundingClientRect().height : 0;
    
    // Calculate the element's position relative to the viewport
    const elementPosition = productsSection.getBoundingClientRect().top;
    const offsetPosition = elementPosition + window.pageYOffset - (headerHeight + 20);

    // Smooth scroll to element with offset
    window.scrollTo({
      top: offsetPosition,
      behavior: 'smooth'
    });
  }
};

// Default smooth scrolling to any section
export const scrollToSection = (sectionId: string) => {
  const element = document.getElementById(sectionId);
  if (element) {
    const navbar = document.querySelector('nav');
    const navbarHeight = navbar ? navbar.getBoundingClientRect().height : 64;
    
    // Use browser's default smooth scrolling
    element.scrollIntoView({ 
      behavior: 'smooth', 
      block: 'start',
      inline: 'nearest'
    });
    
    // Fine-tune position to account for fixed navbar
    setTimeout(() => {
      const elementPosition = element.getBoundingClientRect().top + window.scrollY;
      const offsetPosition = elementPosition - navbarHeight - 16;
      
      window.scrollTo({
        top: offsetPosition,
        behavior: 'smooth'
      });
    }, 100);
  }
};

// Enable smooth scrolling for the entire document
export const enableSmoothScrolling = () => {
  // Add smooth scrolling CSS to html element
  if (typeof document !== 'undefined') {
    document.documentElement.style.scrollBehavior = 'smooth';
  }
};

// Default page scrolling with keyboard support
export const initializeDefaultScrolling = () => {
  if (typeof window === 'undefined') return;

  // Enable smooth scrolling
  enableSmoothScrolling();
  
  // Handle hash changes in URL
  const handleHashChange = () => {
    const hash = window.location.hash.substring(1);
    if (hash) {
      setTimeout(() => scrollToSection(hash), 100);
    }
  };

  // Handle initial hash on page load
  if (window.location.hash) {
    handleHashChange();
  }

  // Listen for hash changes
  window.addEventListener('hashchange', handleHashChange);
  
  return () => {
    window.removeEventListener('hashchange', handleHashChange);
  };
};
